import { reactive, watch, ref } from "vue";
import { useStore } from "vuex";
import Drawer from "./component/drawer.vue";
import Control from "./component/control.vue";
export default {
  name: "music",

  setup() {
    // 
    let list_startMore = reactive([]);
    let list_endMore = reactive([]);
    let drawer = ref(false);
    let musicItem = reactive({
      name: "",
      singer: "",
      id: 0,
      imageUrl: "",
      playUrl: ""
    }); // 

    let moList = [{
      name: "",
      url: require("@/assets/img/likeMo1.webp"),
      introduce: ", "
    }, {
      name: "",
      url: require("@/assets/img/movie3.webp"),
      introduce: ","
    }, {
      name: "",
      url: require("@/assets/img/movie7.webp"),
      introduce: ","
    }, {
      name: "",
      url: require("@/assets/img/likeMov2.webp"),
      introduce: ""
    }, {
      name: "",
      url: require("@/assets/img/movie4.webp"),
      introduce: ""
    }, {
      name: "",
      url: require("@/assets/img/movie5.webp"),
      introduce: ""
    }, {
      name: "",
      url: require("@/assets/img/movie8.webp"),
      introduce: ""
    }]; // vuex

    const store = useStore();

    function getMusicIdEmit(id) {
      console.log(store.state.playStatus);

      if (store.state.playStatus.playId == id && store.state.playStatus.state) {
        console.log("@");
      } else {
        store.commit("updatePauseStatus", false);

        if (id >= 8) {
          list_endMore.forEach(item => {
            if (item.id == id) {
              musicItem.id = item.id;
              musicItem.name = item.name;
              musicItem.imageUrl = item.imageUrl;
              musicItem.playUrl = item.playUrl;
              musicItem.singer = item.singer;
            }
          });
        } else {
          list_startMore.forEach(item => {
            if (item.id == id) {
              musicItem.id = item.id;
              musicItem.name = item.name;
              musicItem.imageUrl = item.imageUrl;
              musicItem.playUrl = item.playUrl;
              musicItem.singer = item.singer;
            }
          });
        }
      }

      drawer.value = true;
    }

    watch(() => store.state.musicInfoLists, newV => {
      list_endMore = newV.slice(8);
      list_startMore = newV.slice(0, 8);
    }, {
      immediate: true
    });
    return {
      moList,
      musicItem,
      list_startMore,
      list_endMore,
      drawer,
      getMusicIdEmit
    };
  },

  components: {
    Drawer,
    Control
  }
};